<template>
	<div>nowplaying

	<ul>
		<!-- <router-link v-for="data in datalist" to="/detail" tag="li">
			{{data}}   （声明式导航）
		</router-link> -->

		<li v-for="(data,index) in datalist" @click="handleClick(index)">
			{{data}}
		</li>
	</ul>

	</div>
</template>

<script>

import router from "@/router";
export default {

  name: 'nowplaying',

  data () {
    return {
    	datalist:["1111","22222","3333","44444"]
    }
  },

  methods:{
  	handleClick(index){
  		//js 实现路由跳转 ---> 编程式导航
  		//
  		//router.push(`/detail/${index}`); // /detail/
  	
  		router.push({name:"mydetail",params:{id:index}});
  	}
  }
}
</script>

<style lang="css" scoped>
</style>


